<script>
export default {
  name:'Home',
  data: () => ({
    isShow: false,
    flag:false,
    show:false
  }),
  methods:{
    logOut(){
            window.localStorage.removeItem("token");
            this.$router.push('/login')
        }
  },
  computed:{
        isLogin(){
            return  localStorage.getItem("token") =='44b4997c9a42b02e2ed3056fcdc80878';
        }

    },
  beforeRouteEnter(to, from, next) {
    if (localStorage.getItem("token")=='44b4997c9a42b02e2ed3056fcdc80878') {
      next();
    } else {
      next("/login");
    }
  },
};
